@import url(https://fonts.googleapis.com/css?family=Poppins);
body {
  background: #002642;
  margin: 0;
  font-family: Poppins, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.bar {
  display: inline-block;
  fill: skyblue;
  margin: 2px;
}

.bar:hover {
  fill: brown;
}

.tooltip {
  position: absolute;
  text-align: center;
  width: 60px;
  height: auto;
  padding: 2px;
  font: 12px sans-serif;
  background: whitesmoke;
  border: 0;
  border-radius: 8px;
  pointer-events: none;
}

.dot {
  stroke: #000;
  opacity: 0.8;
}

div.tooltip {
  position: absolute;
  padding: 10px;
  font: 12px sans-serif;
  background: lightsteelblue;
  border: 0px;
  border-radius: 8px;
  pointer-events: none;
}

.tooltip {
  position: absolute;
  padding: 10px;
  font: 12px Arial;
  background: rgba(255, 255, 204, 0.9);
  box-shadow: 1px 1px 10px rgba(128, 128, 128, 0.6);
  border: 0px;
  border-radius: 2px;
  pointer-events: none;
}

.county {
  /* fill: none; */
}
.state {
  fill: none;
  stroke: black;
  stroke-linejoin: round;
  stroke-width: 0.5;
}

svg {
  font: 10px sans-serif;
  text-align: center;
  display: block;
  margin: auto;
}

.tile-text {
  cursor: default;
}

.App {
  background: #002642;
  min-height: 100vh;
  display: grid;
}

.visualization-selectors {
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 30px 30px;
  gap: 30px 30px;
  padding-top: 100px;
  width: 60vw;
}

header {
  position: absolute;
  top: 0;
  left: 0;
  background: #002642;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  height: 64px;
  width: 100%;
  text-align: left;
  font-size: 32px;
  color: white;
  font-weight: bolder;
  padding-top: 10px;
}

header span {
  padding-left: 30px;
}

h3 {
  margin: 0;
}

.container {
  width: 100%;
  background: whitesmoke;
  text-align: center;
  min-height: 100vh;
  margin-top: 70px;
}

button {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-row-gap: 10px;
  row-gap: 10px;
  background: #162039;
  color: white;
  font-size: 26px;
  border: 5px solid #ffffff;
  border-radius: 4px;
  padding: 10px;
}
button:hover {
  background: #293d6e;
}

svg {
  background-color: whitesmoke;
  text-align: center;
}

